.desktop-container {
    
}

.nav-desktop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(34, 34, 34);
    width: 100%;
    height: 120px;
    box-sizing: border-box;
}

.logo-desktop {
    width: 100px;
    height: auto;
    margin-top: 10px;
    padding: 0 20px;
}

.desktop-name {
    margin: 0;
    padding: 0;
    font-size: 50px;
    color: rgb(145, 28, 28);
    letter-spacing: 9px;
    margin-left: 400px;
    cursor: default;
}

.desktop-menu ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 60px;
    padding: 0 80px;
}

.desktop-menu li {
    font-size: 24px;
    color: white;
    cursor: pointer;
    transition: color 0.5s ease;
}

.desktop-menu li:hover {
    color: rgb(75, 0, 0);
}

.desktop-body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 86.96vh;
    overflow: hidden;
}

.left-container, .right-container {
    flex: 1;
    display: flex;
    height: 100%;
}

.logo-desktop-body {
    max-width: 400px;
    height: auto;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

.left-container {
    background-color: black;
    clip-path: polygon(0 0, 100% 0, 85% 100%, 0 100%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.mono-image1, .mono-image2 {
    width: 100%;
    height: 100%;
    object-fit: cover; 
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    transition: opacity 3s ease-in-out;
}

.mono-image1 {
    animation: fade 20s infinite;
}

.mono-image2 {
    animation: fade2 20s infinite;
}

@keyframes fade {
    0%, 50% {
        opacity: 1;
    }
    51%, 100% {
        opacity: 0;
    }
}

@keyframes fade2 {
    0%, 50% {
        opacity: 0;
    }
    51%, 100% {
        opacity: 1;
    }
}


.desktop-filter {
    background-color: rgba(182, 100, 100, 0.5);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.right-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    color: white;
    margin-bottom: 100px;
}

.right-container h1 {
    font-size: 36px;
    margin-bottom: 20px; 
    color: rgb(255, 255, 255); 
    text-transform: uppercase;
    letter-spacing: 2px; 
    margin-left: 150px; 
}

.right-container p {
    font-size: 20px;
    line-height: 2; 
    max-width: 600px; 
    margin-left: 250px; 
}